.contact-container {
  padding-top: 10%;
  margin-top: -10%;
  min-height: 50vw;
  position: relative;
  overflow: hidden;

  .contact-image {
    position: absolute;
    width: 100%;
    bottom: 0;
  }

  .contact-wrapper {
    padding-right: 20%;
    float: right;
    color: #93da9e;
    z-index: 100;
    position: relative;
    justify-content: center;
    display: flex;
    flex-direction: column;
    height: 35vw;

    .contact-title {
      margin-bottom: 5%;
      color: $fontColor;
      opacity: 0;
      font-family: "Caveat Brush";
      &.bounce {
        opacity: 1;
        animation: bounce 2s ease;
        transition: all 2s;
      }
    }

    .contact-details {
      opacity: 0;
      margin: 1vw 0;
      display: flex;
      align-items: center;
      font-size: 1.4vw;
      filter: blur(5px);
      transition: all 2.5s;
      cursor: pointer;
      position: relative;

      .contact-info {
        position: relative;

        &:after {
          margin-bottom: -3px;
          content: "";
          position: absolute;
          width: 100%;
          transform: scaleX(0);
          height: 2px;
          bottom: 0;
          left: 0;
          background: #93da9e;
          transform-origin: bottom right;
          transition: transform 0.25s ease-out;
        }

        &:hover:after {
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }

      .contact-icon {
        width: 2vw;
        margin-right: 15px;
      }

      &.show {
        opacity: 1;
        filter: blur(0);
        transform: translateY(0);
      }
    }
  }
}

@mixin meteor-animation($delay) {
  opacity: 0;
  animation: move 4s infinite;

  @keyframes move {
    0% {
      opacity: 0;
      bottom: 100%;
      right: -20%;
    }
    20% {
      opacity: 1;
      bottom: 0;
      right: 25%;
    }
    30%,
    100% {
      opacity: 0;
      bottom: 0;
      right: 25%;
    }
  }
}

.meteor1 {
  @include meteor-animation(0);
}

.meteor2 {
  @include meteor-animation(2.5s);
}

.meteor3 {
  @include meteor-animation(1.5s);
}

a {
  color: inherit;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .contact-details {
    font-size: 2vw;
  }
  .contact-wrapper {
    padding-right: 12vw;
  }
}
